export default{
	props:{
		width:{
			type : String,
			default : "100%"
		},
		height:{
			type : String,
			default : "300rpx"
		},
		swiperItems : {
			type : Array,
			default : function(){return new Array();}
		},
		borderRadius : {
			type : String,
			default : '5rpx'
		},
		indicatorColor : {
			type : String,
			default : "rgba(0, 0, 0, 0.2)"
		},
		indicatorActiveColor : {
			type : String,
			default : "#3688FF"
		},
		spacing : {
			type : String,
			default : "50rpx"
		},
		interval : {
			type : Number,
			default : 5000
		},
		padding:{
			type : String,
			default : '0 10rpx'
		},
		scale : {
			type : Boolean,
			default : true
		}
	},
	data() {
		return {
			current : 0
		}
	},
	methods:{
		swiperchange : function (e) {
			this.current = e.detail.current;	
		}
	}
}
